
  .aboutMe{
    margin-bottom: 2vw;
  }
  .interest{
    text-align: center;
  }
  
  .video-grid{
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto auto;
  }
  .left-video{
    justify-self: right;
    margin-left: 1rem;
    width: 30rem;
    height: 17rem;
  }
  .right-video{
    justify-self: left;
    margin-right: 1rem;
    width: 30rem;
    height: 17rem;
  }
  @media only screen and (max-width: 480px) {
    .right-video{
      width: 25vw;
      height: 15vw;
      justify-self: left;
      margin: 1vw;
    }
    .left-video{
      margin: 1vw;
      width: 25vw;
      height: 15vw;
      justify-self: right;
    }
  }